import renderBaseStructure from './modules/renderBaseStructure';
import createField from './modules/createField';
import renderField from './modules/renderField';
import movesControl from './modules/movesControl';
import sizeBtns from './modules/sizeBtns';
import startGameBtn from './modules/startGameBtn';

document.addEventListener('DOMContentLoaded', (event) => {
    let array = createField();
    
    renderBaseStructure();

    renderField(array);

    movesControl(array);

    sizeBtns();
    
    startGameBtn();
});